import React from 'react'
import Badge from 'react-bootstrap/Badge';
import { isHarmGone } from '../js/main.js'

export default function Grid(props) {
    let harms = isHarmGone(props.empty, props.grave, props.harm, props.bik, props.tin_tin_gone, props.floor_tin_gone)
    let goodBadGoneColor = props.goodBadGoneColor
    let goneCSS = props.styleName

    let remarks = ""

    if (goodBadGoneColor)
        if (harms)
            goneCSS = 'bad_gone'
        else
            goneCSS = 'good_gone'
    if (props.yearGone == props.gone) {
        remarks = '年干'
    }
    else if (props.monthGone == props.gone) {
        remarks = '月干'
    }
    else if (props.dayGone == props.gone) {
        remarks = '日干'
    }
    else if (props.timeGone == props.gone) {
        remarks = '時干'
    }


    return (
        <div className={`grid ${goneCSS} ${props.wong ? 'wong_gong' : ''}`} onClick={() => props.updateGakGok(props.gak_gok)}>
            <span className='dis_prop'>{props.empty ? "空" : ""}</span>
            <span className='dis_prop'>{props.horse ? "馬" : ""}</span>
            <span></span>
            <span className={`
                water_mark 
                ${remarks === '年干' && props.sameGone.has('year') ? 'same_year' : ''}
                ${remarks === '月干' && props.sameGone.has('month') ? 'same_month' : ''}
                ${remarks === '日干' && props.sameGone.has('day') ? 'same_day' : ''}
                ${remarks === '時干' && props.sameGone.has('time') ? 'same_time' : ''}
            `}>{remarks === '' ? props.gone : remarks}</span>
            {/* <span className='wide_grid'>
                <input type='text' className='grid_remarks' onClick={(e)=>{e.preventDefault()}} />
            </span> */}

            <span className='intro_prop'>{props.yan_gone_pri}</span>
            <span className={
                //main gone
                props.sun === '符' && (
                    props.day_gone === '甲'
                )
                    ?
                    (harms || props.fan_or_fok ? 'harm_main_gone' : 'main_gone') : ''
                        //targets
                        + props.targets.length > 0 && props.targets.indexOf(props.sun) > -1 ? ' targets' : ''
            }
            >{props.sun}</span>
            <span></span>
            <span>{props.floor_sun}</span>

            <span className='intro_prop'>{props.yan_gone_sec}</span>
            <span className={props.targets.length > 0 && props.targets.indexOf(props.display_star) > -1 ? 'targets' : ''}>{props.display_star}</span>
            <span className='dis_prop'>{props.grave ? "墓" : ""}</span>
            <span className={
                //main gone
                props.tin_tin_gone.indexOf(props.day_gone) >= 0
                    ?
                    (harms || props.fan_or_fok ? 'harm_main_gone' : 'main_gone') : ''
                        //targets
                        + props.targets.length > 0 && props.targets.indexOf(props.tin_tin_gone) > -1 ? ' targets' : ''
                            + props.targets.length > 0 && props.tin_tin_gone.length === 2 && (props.targets.indexOf(props.tin_tin_gone[0]) > -1 || props.targets.indexOf(props.tin_tin_gone[1]) > -1) ? ' targets' : ''
            }>{props.tin_tin_gone}</span>

            <span className='dis_prop'>{props.bik ? "迫" : ""}</span>
            <span className={props.targets.length > 0 && props.targets.indexOf(props.door) > -1 ? 'targets' : ''}>{props.door}</span>
            <span className='dis_prop'>{props.harm ? "刑" : ""}</span>
            <span>{props.floor_tin_gone}</span>
        </div>
    )
}
