const theif_gok = ["庚", "丙"]
const change_gok = ["庚", "戊"]
const leave_gok = ["乙", "辛"]

const doors_equals = (door, door2) => {
    return door == door2
}


const gone_equals = (gone, gone2) => {
    return gone == gone2
}

const is_door_fok = (door, origin_door, gone, gone2) => {
    return doors_equals(door, origin_door)
}

const is_gone_fok = (gone, gone2) => {
    return gone_equals(gone, gone2)
}

const is_door_fan = (door, origin_door) => {
    return doors_equals(door, origin_door)
}

const is_gone_fan = (gone, gone2) => {
    return gone_equals(gone, gone2)
}

const conflicts = (tin_gone, floor_gone) => {
    let result = false

    conflicts_gone.forEach((c) => {
        if (tin_gone.indexOf(c.main) > -1 || floor_gone.indexOf(c.main) > -1) {
            c.target.forEach((t) => {



                if (tin_gone.indexOf(t) > -1 || floor_gone.indexOf(t) > -1)
                    result = true
            })
        }
    })

    return result

}

const merges = (tin_gone, floor_gone, tin_sun, floor_sun) => {
    let result = false

    merge_gone.forEach((c) => {
        if (tin_gone.indexOf(c.main) > -1 || floor_gone.indexOf(c.main) > -1 ||
            tin_sun.indexOf(c.main) > -1 || floor_sun.indexOf(c.main) > -1
        ) {
            if (tin_gone.indexOf(c.target) > -1 || floor_gone.indexOf(c.target) > -1)
                result = true
        }
    })

    return result

}

const theifs = (tin_gone, floor_gone) => {
    let result = false

    if (tin_gone.indexOf(theif_gok[0]) > -1 && tin_gone.indexOf(theif_gok[1]) > -1) //天自成賊局
        result = true
    else if (floor_gone.indexOf(theif_gok[0]) > -1 && floor_gone.indexOf(theif_gok[1]) > -1)//地自成賊局
        result = true
    else if (tin_gone.indexOf(theif_gok[0]) > -1 && floor_gone.indexOf(theif_gok[1]) > -1)
        result = true
    else if (tin_gone.indexOf(theif_gok[1]) > -1 && floor_gone.indexOf(theif_gok[0]) > -1)
        result = true

    return result
}

const changes = (tin_gone, floor_gone) => {
    let result = false

    if (tin_gone.indexOf(change_gok[0]) > -1 && tin_gone.indexOf(change_gok[1]) > -1) //天自成賊局
        result = true
    else if (floor_gone.indexOf(change_gok[0]) > -1 && floor_gone.indexOf(change_gok[1]) > -1)//地自成賊局
        result = true
    else if (tin_gone.indexOf(change_gok[0]) > -1 && floor_gone.indexOf(change_gok[1]) > -1)
        result = true
    else if (tin_gone.indexOf(change_gok[1]) > -1 && floor_gone.indexOf(change_gok[0]) > -1)
        result = true

    return result
}

const leaves = (tin_gone, floor_gone) => {
    let result = false

    if (tin_gone.indexOf(leave_gok[0]) > -1 && tin_gone.indexOf(leave_gok[1]) > -1) //天自成賊局
        result = true
    else if (floor_gone.indexOf(leave_gok[0]) > -1 && floor_gone.indexOf(leave_gok[1]) > -1)//地自成賊局
        result = true
    else if (tin_gone.indexOf(leave_gok[0]) > -1 && floor_gone.indexOf(leave_gok[1]) > -1)
        result = true
    else if (tin_gone.indexOf(leave_gok[1]) > -1 && floor_gone.indexOf(leave_gok[0]) > -1)
        result = true

    return result
}

const gakgoks = (tin_gone, floor_gone) => {
    let result = []

    gak_gok_81.forEach(g => {
        if
            (
            tin_gone.length === 2 &&
            (tin_gone.indexOf(g.primary_tin_gone) === 0 && tin_gone.indexOf(g.secondary_tin_gone) === 1)
        )
            result.push(g)
        else if (
            floor_gone.length === 2 &&
            (floor_gone.indexOf(g.primary_tin_gone) === 0 && floor_gone.indexOf(g.secondary_tin_gone) === 1)
        )
            result.push(g)
        else {
            if (tin_gone.indexOf(g.primary_tin_gone) > -1 && floor_gone.indexOf(g.secondary_tin_gone) > -1)
                result.push(g)
        }
    })

    return result
}


const is_tin_gone_conflicts = (tins, floors, door_sun_seq) => {
    let result = []
    let t_tin = []
    t_tin.push(...tins)
    t_tin.pop()

    t_tin.forEach((t, idx) => {
        if (conflicts(t['primary'] + t['second'], floors[idx]))
            result.push(door_sun_seq[idx])
    })

    return result;
}

const is_tin_gone_merges = (tins, floors, tin_suns, floor_suns, door_sun_seq) => {
    let result = []
    let t_tin = []
    t_tin.push(...tins)
    t_tin.pop()

    t_tin.forEach((t, idx) => {
        if (merges(t['primary'] + t['second'], floors[idx], tin_suns[idx], floor_suns[idx]))
            result.push(door_sun_seq[idx])
    })

    return result;
}

const is_theif_gok = (tins, floors, door_sun_seq) => {
    let result = []
    let t_tin = []
    t_tin.push(...tins)
    t_tin.pop()

    t_tin.forEach((t, idx) => {
        if (theifs(floors[idx], t['primary'] + t['second']))
            result.push(door_sun_seq[idx])
    })

    return result;
}

const is_change_gok = (tins, floors, door_sun_seq) => {
    let result = []
    let t_tin = []
    t_tin.push(...tins)
    t_tin.pop()

    t_tin.forEach((t, idx) => {
        if (changes(floors[idx], t['primary'] + t['second']))
            result.push(door_sun_seq[idx])
    })

    return result;
}

const is_leave_gok = (tins, floors, door_sun_seq) => {
    let result = []
    let t_tin = []
    t_tin.push(...tins)
    t_tin.pop()

    t_tin.forEach((t, idx) => {
        if (leaves(floors[idx], t['primary'] + t['second']))
            result.push(door_sun_seq[idx])
    })

    return result;
}

const find_gak_gok = (tins, floors, door_sun_seq) => {
    let result = []
    let t_tin = []
    t_tin.push(...tins)
    t_tin.pop()

    t_tin.forEach((t, idx) => {
        result.push({
            seq: door_sun_seq[idx],
            gakgok: gakgoks(floors[idx], t['primary'] + t['second'])
        })
    })

    return result;
}

export {
    is_door_fok, is_gone_fok, is_door_fan, is_gone_fan, is_tin_gone_conflicts, is_tin_gone_merges,
    is_theif_gok, is_change_gok, is_leave_gok, theifs, find_gak_gok
}