import React from 'react'
import {
  tin_gone_seq, star_seq, door_seq, door_sun_seq,
  find_gok, floor_tin_gone, find_empty_and_chun, find_door_and_star,
  get_floor_tin_gone_display, get_star_display, get_tin_tin_gone_display, get_door_display, get_sun_display, get_floor_sun_display,
  find_empty_gong, find_harm, find_grave, find_bik, find_horse, find_yan_gone,
  spinL,spinR
} from "../js/main.js"

import {
  is_door_fok, is_gone_fok, is_door_fan, is_gone_fan, is_tin_gone_conflicts, is_tin_gone_merges,
  is_theif_gok,is_change_gok,is_leave_gok,find_gak_gok
} from '../js/notes.js'

export default function Notes(props) {
  let time_gone = props.time_gone
  let time_chi = props.time_chi
  let time_gone_chi = time_gone + time_chi

  let empty_and_chun = find_empty_and_chun(time_gone_chi);
  let gok = find_gok(props.year_chi, props.month_chi, props.day, props.time_chi)
  let yinyang = props.yinyang



  let chun_gone = empty_and_chun['chun_gone']
  let chun_chi = empty_and_chun['chun_chi']

  let empty_gong1 = find_empty_gong(empty_and_chun['empty1'])
  let empty_gong2 = find_empty_gong(empty_and_chun['empty2'])

  let door_and_star = find_door_and_star(floor_tin_gone(gok, yinyang), chun_gone)

  let floor_tin_gone_seq = floor_tin_gone(gok, yinyang)
  let display_floor_tin_gone_array = get_floor_tin_gone_display(floor_tin_gone_seq)

  // 

  let display_star_array = get_star_display(display_floor_tin_gone_array, time_gone, time_chi, door_and_star['star'])
  let display_tin_tin_gone_array = get_tin_tin_gone_display(display_star_array, display_floor_tin_gone_array)

  let display_door_array = get_door_display(display_floor_tin_gone_array, chun_gone, chun_chi, time_chi, yinyang, door_and_star['door'])

  let display_sun_array = get_sun_display(display_tin_tin_gone_array, chun_gone, yinyang)
  let display_floor_sun_array = get_floor_sun_display(display_floor_tin_gone_array, chun_gone, yinyang)

  let horse = find_horse(time_chi)

  let door_gong_idx = 0

  display_door_array.forEach((d, idx) => {
    if (d == door_and_star['door'])
      door_gong_idx = idx
  })



  let yan_gone_array = find_yan_gone(display_tin_tin_gone_array, time_gone, time_chi, display_tin_tin_gone_array[door_gong_idx], chun_gone, yinyang, door_gong_idx)

  //for remarks
  let dataSetObj = {
    display_floor_tin_gone_array:display_floor_tin_gone_array,
    display_tin_tin_gone_array:display_tin_tin_gone_array,
    display_sun_array:display_sun_array,
    display_floor_sun_array:display_floor_sun_array,
    display_star_array:display_star_array,
    display_door_array:display_door_array,
    yan_gone_array:yan_gone_array
  }

  const updateDataSet = (obj) =>{
    display_floor_tin_gone_array=obj.display_floor_tin_gone_array
    display_tin_tin_gone_array=obj.display_tin_tin_gone_array
    display_sun_array=obj.display_sun_array
    display_floor_sun_array=obj.display_floor_sun_array
    display_star_array=obj.display_star_array
    display_door_array=obj.display_door_array
    yan_gone_array=obj.yan_gone_array
  }

  if(!props.spin){
    props.initOriginalGridDataSet(dataSetObj)
    updateDataSet(dataSetObj)
  }
  else{
    if(props.spinLeftValue > 0 || props.spinRightValue > 0){
      if(props.spinLeftValue > 0){
        updateDataSet(spinL(dataSetObj,props.spinLeftValue))
      }
      else if(props.spinRightValue > 0){
        
        updateDataSet(spinR(dataSetObj,props.spinRightValue))
      }
    }
    else{
      
      updateDataSet(dataSetObj)
    }
  }
  let door_fok = is_door_fok(display_door_array[0], door_seq[0]) ? "八門伏吟" : ""

  let gone_fok = is_gone_fok(display_floor_tin_gone_array[0]['primary'], display_tin_tin_gone_array[0]) ? "天干伏吟" : ""

  let door_fan = is_door_fan(display_door_array[0], door_seq[4]) ? "八門反吟" : ""
  let gone_fan = is_gone_fan(display_floor_tin_gone_array[0]['primary'], display_tin_tin_gone_array[4]) ? " 天干反吟" : ""

  const notes = [door_fok, gone_fok, door_fan, gone_fan]
  const conflicts = is_tin_gone_conflicts(display_floor_tin_gone_array, display_tin_tin_gone_array, door_sun_seq)
  const merges = is_tin_gone_merges(display_floor_tin_gone_array, display_tin_tin_gone_array, display_sun_array, display_floor_sun_array, door_sun_seq)
  const theifs = is_theif_gok(display_floor_tin_gone_array,display_tin_tin_gone_array,door_sun_seq)
  const changes = is_change_gok(display_floor_tin_gone_array,display_tin_tin_gone_array,door_sun_seq)
  const leaves = is_leave_gok(display_floor_tin_gone_array,display_tin_tin_gone_array,door_sun_seq)

  return (
    <div className='alert alert-warning'>
      <div className='message'>
        {
          notes.map((n) => {
            if (n != "")
              return (
                <span className='harm_notes' key={`notes_${n}`}>{n}</span>
              )
          })
        }
      </div>
      <div className='message'>
        {
          conflicts.map((n) => {
            if (n != "")
              return (
                <span key={`conflicts_${n}`}>[{n}] 天干相沖</span>
              )
          })
        }
      </div>
      <div className='message'>
        {
          merges.map((n) => {
            if (n != "")
              return (
                <span key={`merges_${n}`}>[{n}] 天干相合</span>
              )
          })
        }
      </div>
      <div className='message'>
        {
          theifs.map((n) => {
            if (n != "")
              return (
                <span className='harm_notes' key={`theifs_${n}`}>[{n}] 賊局</span>
              )
          })
        }
      </div>
      <div className='message'>
        {
          changes.map((n) => {
            if (n != "")
              return (
                  <span key={`changes_${n}`}>[{n}] 換</span>
              )
          })
        }
      </div>
      <div className='message'>
        {
          leaves.map((n) => {
            if (n != "")
              return (
                <span className='harm_notes' key={`leaves_${n}`}>[{n}] 離散</span>
              )
          })
        }
      </div>
    </div>
  )
}
