import solar from './solar.json'
const solar_JSON_starting_year = 1900

const find_solar_terms = async (terms, year) => {
    const solar_term_api_url = `https://api.timelessq.com/time/jieqi?year=${year}`


    const response = await fetch(solar_term_api_url);
    const data = await response.json();

    let result = new Date()

    data['data'].forEach((d) => {
        if (d['name'] == terms) {

            result = new Date(d['time'])
        }
    });

    return result
}

const find_solar_terms_offline = async (terms, year) => {
    const data = solar[year - solar_JSON_starting_year]
    let result = new Date()

    data['data'].forEach((d) => {
        if (d['name'] == terms) {

            result = new Date(d['time'])
        }
    });

    return result
}

const find_current_solar_terms = async (months, year) => {

    const last_year_solar_term_api_url = `https://api.timelessq.com/time/jieqi?year=${year - 1}`
    const solar_term_api_url = `https://api.timelessq.com/time/jieqi?year=${year}`


    const last_year_resp = await fetch(last_year_solar_term_api_url);
    const last_year_data = await last_year_resp.json();

    const response = await fetch(solar_term_api_url);
    const data = await response.json();

    let last_year_last_st;
    let array_current_year = []


    last_year_data['data'].forEach((d, idx) => {
        if (idx == 24) {
            let t_date = new Date(d['time'])
            last_year_last_st = t_date
        }
    })



    data['data'].forEach((d, idx) => {
        if (idx == 0 || idx % 2 == 0) {
            let t_date = new Date(d['time'])
            if (t_date.getFullYear() == year)
                array_current_year.push(t_date)
        }
    });



    return { first: months == 0 ? last_year_last_st : array_current_year[months - 1], second: array_current_year[months] }
}

const find_current_solar_terms_offline = async (months, year) => {

    const last_year_data = solar[year - solar_JSON_starting_year - 1]

    const data = solar[year - solar_JSON_starting_year]

    let last_year_last_st;
    let array_current_year = []


    last_year_data['data'].forEach((d, idx) => {
        if (idx == 24) {
            let t_date = new Date(d['time'])
            last_year_last_st = t_date
        }
    })



    data['data'].forEach((d, idx) => {
        if (idx == 0 || idx % 2 == 0) {
            let t_date = new Date(d['time'])
            if (t_date.getFullYear() == year)
                array_current_year.push(t_date)
        }
    });



    return { first: months == 0 ? last_year_last_st : array_current_year[months - 1], second: array_current_year[months] }
}

const roundToNearestMinute = function (date) {
    var coeff = 1000 * 60 * 1; // <-- Replace {5} with interval

    return new Date(Math.round(date.getTime() / coeff) * coeff);
};

export { find_solar_terms, find_current_solar_terms, find_solar_terms_offline, find_current_solar_terms_offline, roundToNearestMinute }