const find_harm = (gong, tin_gone, floor_gone) => {
    let harmful = false
    harm.forEach((h) => {
        if (h['gong'] == gong) {
            h['chi'].forEach((h) => {
                if (tin_gone.indexOf(h) > -1 || floor_gone.indexOf(h) > -1)
                    harmful = true
            })
        }
    })

    if (tin_gone.indexOf("辛") > -1 && floor_gone.indexOf("辛") > -1)
        harmful = true
    if (tin_gone.indexOf("壬") > -1 && floor_gone.indexOf("壬") > -1)
        harmful = true

    return harmful
}

const find_grave = (gong, tin_gone, floor_gone, sun, floor_sun) => {
    let is_grave = false
    grave.forEach((g) => {
        if (g['gong'] == gong) {
            g['chi'].forEach((g) => {
                if (tin_gone.indexOf(g) > -1 || floor_gone.indexOf(g) > -1)
                    is_grave = true
            })

            if (g['sun'].includes(sun) || g['sun'].includes(floor_sun))
                is_grave = true
        }
    })

    return is_grave
}

