const nearestDates = (d) => {
    let dates = Array()
    let now = new Date()
    now.setHours(0)
    now.setMinutes(0)
    now.setSeconds(0)

    let orig_d = d
    let past = (now.getTime() - orig_d.toDate().getTime()) / (1000 * 3600 * 24) >= 9
    let limit = past ? 30 : 30
    if (past)
        orig_d = lunisolar(now)

    for (let i = 0; i < limit; i++) {
        let temp_d = orig_d.add(i, 'd')

        let _gone = -1
        gone_chi.forEach((g) => { if (g.chi.indexOf(temp_d.char8.day.branch.toString()) > -1) _gone = g.gone })

        let temp_d_obj = temp_d.toDate()
        temp_d_obj.setHours(0)
        temp_d_obj.setMinutes(0)
        temp_d_obj.setSeconds(0)
        let orig_d_obj = orig_d.toDate()
        orig_d_obj.setHours(0)
        orig_d_obj.setMinutes(0)
        orig_d_obj.setSeconds(0)

        dates.push({
            gone: _gone,
            lunar_day_chi: temp_d.char8.day.branch.toString(),
            date: temp_d.format('YYYY/MM/DD'),
            best_before_date: (temp_d_obj.getTime() - orig_d_obj.getTime()) / (1000 * 3600 * 24) < 9 ? true : false,
            yin_yang: floor12_str.indexOf(temp_d.char8.day.branch.toString()) % 2 == 0 ? '陽' : '陰',
            animal: floor12_animarl[floor12_str.indexOf(temp_d.char8.day.branch.toString())],
            past: past
        })
    }

    return dates
}

